import mongoose from 'mongoose';
mongoose.Promise = global.Promise;
import uniqid from 'uniqid';

const Schema = mongoose.Schema;

const OpenBetSchema = new mongoose.Schema({
    orderNumber: {
        type: String,
        default: uniqid.process().toUpperCase()
    },
    betOption: {
        type: String,
        default: 'Straight'
    },
    superAgent: {
        type: String,
        required: true
    },
    agent: {
        type: String,
        required: true
    },
    username: {
        type: String,
        required: true
    },
    events: {
        type: Array,
        required: true
    },
    eventsWagerDetail: {
        type: Object,
        required: true
    },
    orderStatus: {
        type: Object,
        default: 'open'
    },
    createdAt: {
        type: Date,
        default: Date.now
    }
});


const OpenBet = mongoose.model('openBet', OpenBetSchema);

export default OpenBet;